import React, { ReactNode } from 'react';

type ButtonProps = {
  children: ReactNode;
  // add other props as needed, e.g., onClick, className, etc.
};

export default function Button({ children, ...rest }: ButtonProps) {
  return (
    <button {...rest}className='bg-blue-700 text-white font-bold py-2 px-4 rounded hover:bg-blue-800 transition duration-300 ease-in-out justify-center items-center'>  
      {children}
    </button>
  );
}